// scrollbar
::-webkit-scrollbar {
  width: 6px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: #ddd;
}
::-webkit-scrollbar-thumb {
  background: rgba(0,0,0,0.4);
  border-radius: 20px;  
  &:hover{
    background: rgba(0,0,0,0.5);
  }
}
body::-webkit-scrollbar-track-piece{
  background-color: transparent;
}
.body-padding{
  padding-top: 75px;
  
}

// page-transition
.preloader {
  top: -1%;
  height: 102%;
  position: fixed;
  pointer-events: none;
  z-index: 9999;
}
.preloader, .preloader-line {
  left: 0;
  width: 100%;
  overflow: hidden;
}
.preloader.active{
  z-index: 999;
}
.preloader-line {
  position: absolute;
  top: 0;
  height: 33.5%;
}
.preloader-line:nth-child(2) {
  top: 33.3%;
}
.preloader-line:nth-child(3) {
  top: 66.6%;
}
.preloader-line div {
  width: 100%;
  height: 100%;
  background: @primary-color-dark;
  transition: all 2s;
  transform: translate(-100%);
}
.preloader.active .preloader-line > div{
  animation: page 2s ease-in-out;
}
.preloader.active .preloader-line:nth-child(2) > div {
  animation-delay: .1s;
}
.preloader.active .preloader-line:nth-child(3) > div {
  animation-delay: .2s;
}
@keyframes page{
  25% {
    transform: translate(0%);
  }
  50% {
    transform: translate(0%);
  }
  100%{
    transform: translate(100%);
  }
}
.page-scroll{
  height: 100vh;
}
