//btn
.btn-more{
  display: flex;
  justify-content: flext-start;
  align-items: center;
  font-weight: 400;
  & .btn-more-wrap{
    position: relative;    
  }
  & .right{
    padding-left: 1.5rem;
  }
  & i {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 1.625rem;
    height: .9375rem;
    margin: -0.76875rem 0 0 -0.8125rem;
    overflow: hidden;
    transform: rotate(0deg);
    &.arrow-right{
      transform: rotate(90deg);
    }
    &::before,::after{
      content: "";
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: 50% 50% no-repeat;
      background-size: contain;
      transition: transform .3s .2s;
      transform: translateX(0);
      background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 28 17'%3E%3Cpath fill='none' stroke='%230d0d0d' stroke-width='1.5' stroke-linecap='square' d='M1.5 8.5h24m-6-7l7 7m-7 7L26 9'/%3E%3C/svg%3E");
    }
  }
  &.secondary{
    & svg{
      stroke: @secondary-color;
    }
    & i::before,i::after{
      background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 28 17'%3E%3Cpath fill='none' stroke='%23FABF35' stroke-width='1.5' stroke-linecap='square' d='M1.5 8.5h24m-6-7l7 7m-7 7L26 9'/%3E%3C/svg%3E");
    }
  }
  &.white{
    & svg{
      stroke: #fff;
    }
    & i::before,i::after{
      background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 28 17'%3E%3Cpath fill='none' stroke='%23fff' stroke-width='1.5' stroke-linecap='square' d='M1.5 8.5h24m-6-7l7 7m-7 7L26 9'/%3E%3C/svg%3E");
    }
    & p{
      color: #fff;
    }
  }
  & svg{
    stroke: @text-color;
    width: 4.125rem;
    height: 4.125rem;   
    transform: rotate(-180deg);
    transition: transform 1.2s,background-color .3s;
    border-radius: 100%;
    overflow: hidden;
    & circle{
      stroke-dasharray: 267;
      stroke-dashoffset: 267px;
      transition: stroke-dashoffset 1.2s,transform 1.2s;
      transform-origin: 50% 50%;
      &:last-child{
        transform: rotate(-45deg);
      }
    }
  }
  & p{
    margin: 0;
    font-size: 1rem;
  }
  & i{
    display: block;
    position: absolute;
  }
  &.isview{
    transform: rotate(0deg);
    & svg{
      transform: rotate(0deg);
    }
    & svg circle:first-child{
      stroke-dashoffset: 22px;
    }
    
  }
}

// header
#header{
  z-index: 99;
  background-color: #fff;
  transition: all .3s;
  position: fixed;
  width: 100%;
  border-bottom: 1px solid transparent;
  & .header-dropmenu a{
    color: @text-color !important;
  }
  & .ant-row{
    height: 100%;
  }
  & .logo-wrap{
    display: inline-block;
    transition: all .3s;
    overflow: hidden;
    width: 100%;
  }
  & .logo-w-wrap{
    display: inline-block; 
    transition: all .3s;
    overflow: hidden;
    width: 0;
  }
  &.headerTranparent{
    color: #fff;
    background-color: transparent;
    position: fixed;
    & li a{
      color: #fff;
    }
    & .logo-wrap{
      width: 0;
    }
    & .logo-w-wrap{
      width: 100%;
    }
  }
  &.open{
    transition: all .1s;
    border-bottom: 1px solid #ddd;
    background-color: #fff;
    & li a{
      color: @text-color;
      transition-delay: .1s
    }
    & .header-rightline{
      transform: scaleY(0);      
    }
    & .header-bottomline{
      transform: scaleX(0);
    }
    & .logo-wrap{
      width: 100%;
      transition-delay: .3s;
    }
    & .logo-w-wrap{
      width: 0;
    }
  }
  &.scroll {
    position: fixed;
    top: 0;
    & li a{
      color: @text-color;
      transition-delay: .3s
    }
    & .header-bg {
      height: 100%;
    }
    & .header-rightline{
      transform: scaleY(0);      
    }
    & .header-bottomline{
      transform: scaleX(0);
    }
    & .logo-wrap{
      width: 100%;
      transition-delay: .5s;
    }
    & .logo-w-wrap{
      width: 0;
    }
  }
}

