//header-dropmenu
.header-dropbtn{
  position: relative;
  &::after{
    width: 100%;
    height: 1px;
    bottom: 0;
    left: 0;
    right: 0;
    position: absolute;
    transform: scaleX(0);
    transition: all .3s;
    transition-delay: 0.1s;
    content: '';
  }
  // &.open::after{
  //   transform: scaleX(1);
  //   background-color: @secondary-color;
  // }
 
}
.header-dropmenu{
  z-index:99;
  opacity: 0;
  height: auto;
  list-style: none;
  text-align: center; 
  position: absolute;
  width: 200%;
  left: 50%;
  top: 100%;
  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  -webkit-transform: translateX(-50%) scaleY(0);
  -ms-transform: translateX(-50%) scaleY(0);
  transform: translateX(-50%) scaleY(0);
  transform-origin: top;
  padding: 0;
  line-height: 1.6;
  transition: .2s;  
  overflow: hidden;
  
  &.open{
    -webkit-transform: translateX(-50%) scaleY(1);
    -ms-transform: translateX(-50%) scaleY(1);
    transform: translateX(-50%) scaleY(1);
    opacity: 1;
    padding: 1.25rem 0;
    & .header-dropmenu-bg{
      transform: scaleY(1);
    }
  }
  & li{
    padding: 0;
    line-height: 1.6;
    margin-bottom: 0.5rem;
  }
}
.header-dropmenu-bg{
  background-color: #fff;
  transform-origin: top;
  transform: scaleY(0);
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: -1;
  transition: all .2s;
  top:0;
  left: 0;
