    right: 0;
    bottom: 0;
    top: 0;
    background-color: rgba(0,0,0,0.1);
    z-index: -1;
  }
  & .kv-img{
    position: absolute;
    left: 0;
    top:0;
    bottom: auto;
    max-width: 100%;
    width: 100%;
    min-height: 100%;
    object-fit: cover;
    aspect-ratio: auto 1900 / 1144;
    height: calc(100vh + 160px);
    z-index: -1;
  }
  & .btn-more{
    position: absolute;
    bottom: 2%;
    left: 50%;
    transform: translateX(-50%); 
