.project-map-panel-content{
  height: 20.5rem;  
  padding-top: 0;
}
.project-map-panel-scroller{
  overflow-y: auto;
  height: 100%;
  width: 100%;
}
.ant-image-preview-img-wrapper,.ant-image-preview-body{
  box-shadow: none;
}
.gm-style-moc{
  display: none;
}
.project-map-mask{
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.05);
}
.project-photos .my-masonry-grid_column > div{
  margin-bottom: 0;
}
.project-photos-container{
  display: flex;
  flex-wrap: wrap;
}
.project-photos-wrap{
  width: 11.111%;
  font-size: 0;
  position: relative;
  & .ant-image-mask{
    // display: none;
    background:none;
    & .ant-image-mask-info{
      display: none;
    }
  }
  & .ant-image{
    height: 0;
    overflow: hidden;
    padding-top: 60%;    
  }
  & .ant-image-img{
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
  }
  & .ant-image-mask-info{
    font-size: 0.93rem;
  }
  &:hover .project-photos-text{
    opacity: 1;
  }
  &:hover .project-photos-mask{
    background-color: rgba(0, 0, 0,0);
  }
}
.project-photos-mask{
  background-color: rgba(0, 0, 0, 0.4);
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: block;
  z-index: 1;
  transition: .2s;
  pointer-events: none;
}
.project-photos-text{
  cursor: pointer;
  position: absolute;
  pointer-events: none;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  z-index: 9;
  color: #fff;
  padding: 0.5rem;
  background-image: linear-gradient(0, rgba(0,0,0,0.7) 5%,transparent 50%);
  opacity: 0;
  transition: .2s;
  & h4{
    color: #fff;
    font-size: 1rem;
    margin-bottom: 0;
  }
  & p{
    color: #fff;
    font-size: 0.833rem;
    margin-bottom: 0;
  }
}
.ant-image-preview-img-wrapper,.ant-image-preview-content,.ant-image-preview-body{
  height: 100%;
}